import { addMenu } from "./components/menu.js";
import { addFilter } from "./components/filters.js";
import { addBoardFilter } from "./components/boardFilters.js";
import { addCardForm } from "./components/cardForm.js";
import { addCardExample } from "./components/cardExample.js";
import { addBtnLoadMore } from "./components/btnLoadMore.js";

// Variables
const mainContol = document.querySelector('.main__control');
const mainFilter = document.querySelector('.main__filter');
const board = document.querySelector('.board');
const boardTasks = board.querySelector('.board__tasks');


//
// Create page
const createPage = () => {
  mainContol.insertAdjacentHTML('beforeend', addMenu());
  mainFilter.insertAdjacentHTML('afterbegin', addFilter());
  board.insertAdjacentHTML('afterbegin', addBoardFilter());
  board.insertAdjacentHTML('beforeend', addBtnLoadMore());
  boardTasks.insertAdjacentHTML('afterbegin', addCardForm());
  boardTasks.insertAdjacentHTML('beforeend', addCardExample());
};



// Events
window.onload = createPage();


